.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #667799;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

/* @media (min-width: 300px) {
  .main-header {
    justify-content: space-between;
  }
} */

@media (min-width: 200px) {
  .main-header {
    justify-content: space-between;
  }
}

.header-color {
  background: #6495ed;
}